	createStyles({
		root: {
			flex: 1,
			display: "flex",
			width: "100%",
			height: "100%",
		},
		container: {
			flex: 1,
			display: "flex",
			flexDirection: "column",
		},
		grid: {
			flex: 1,
			display: "inline-grid",
			gridTemplateColumns: "repeat(8, 1fr)",
			padding: "20px",
		},
		gridNoWeeks: {
			flex: 1,
			display: "inline-grid",
			gridTemplateColumns: "repeat(7, 1fr)",
			padding: "20px",
		},
		gridWeekDay: {
			display: "inline-grid",
			gridTemplateColumns: "repeat(8, 1fr)",
			paddingTop: "20px",
			paddingLeft: "20px",
			paddingRight: "20px",
		},
		gridWeekDayNoWeeks: {
			display: "inline-grid",
			gridTemplateColumns: "repeat(7, 1fr)",
			paddingTop: "20px",
			paddingLeft: "20px",
			paddingRight: "20px",
		},
		header: {
			textAlign: "center",
			backgroundColor: "#000 !important",
			color: "#FFF !important",
			display: "flex",
		},
		cell: {
			gridColumnEnd: "span 1",
			display: "flex",
			flexDirection: "column",
			border: "solid #333",
			borderWidth: "1px",
			padding: "10px",
		},
		cellLabel: {
			display: "flex",
			justifyContent: "center",
		},
		cellToday: {
			display: "flex",
			flexDirection: "column",
			border: "solid #333",
			borderWidth: "1px",
			padding: "10px",
			backgroundColor: "#111",
		},
		cellContent: {
			display: "flex",
			flexDirection: "row",
		},
		flex1: {
			flex: 1,
		},
		month: {
			fontSize: "2vw",
			fontFamily: "Roboto",
			minWidth: "10vw",
		},
		label: {
			fontSize: "2vw",
			fontFamily: "Roboto",
		},
		date: {
			fontSize: "2vw",
			fontFamily: "Roboto",
			paddingRight: "5px",
		},
		week: {
			fontSize: "2vw",
			fontFamily: "Roboto",
			gridColumnEnd: "span 1",
		},
		moon: {
			fontSize: "2vw",
			fontFamily: "Roboto",
		},
		mobileStepper: {
			fontSize: "2vw",
			fontFamily: "Roboto",
			backgroundColor: "#000 !important",
			color: "#FFF !important",
		},
		direction: {
			fontSize: "3vw",
			fontFamily: "Roboto",
			minWidth: "20vw",
		},
		menu: {
			display: "flex",
		},
	});

const daysInMonth = (iMonth: number, iYear: number) => {
	return 32 - new Date(iYear, iMonth, 32).getDate();
};

const Moon = (year: number, month: number, day: number) => {
	const phases = ["", "", "", "", "", "", "", ""];

	let c: number = 0;
	let e: number = 0;
	let jd: number = 0;
	let b: number = 0;

	if (month < 3) {
		year--;
		month += 12;
	}

	++month;

	c = 365.25 * year;
	e = 30.6 * month;
	jd = c + e + day - 694039.09; // jd is total days elapsed
	jd /= 29.5305882; // divide by the moon cycle
	b = Math.floor(jd); // int(jd) -> b, take integer part of jd
	jd -= b; // subtract integer part to leave fractional part of original jd
	b = Math.round(jd * 8); // scale fraction from 0-8 and round

	if (b >= 8) b = 0; // 0 and 8 are the same so turn 8 into 0

	return { phase: b, emoji: phases[b] };
};

interface Day {
	label: String;
	date: number;
}

interface IProps {
	classes: any;
	theme: any;
}

const Calendar = (props: IProps) => {
	const today = new Date();
	const [activeStep, setActiveStep] = React.useState(today.getMonth());
	const [open, setOpen] = React.useState(false);
	const [enableMoon, setEnableMoon] = React.useState(false);
	const [enableWeeks, setEnableWeeks] = React.useState(true);
	const { classes, theme } = props;

	const handleDrawerOpen = () => {
		setOpen(true);
	};

	const handleDrawerClose = () => {
		setOpen(false);
	};

	const monthLabels = [
		"Januari",
		"Februari",
		"Mars",
		"April",
		"Maj",
		"Juni",
		"Juli",
		"Augusti",
		"September",
		"Oktober",
		"November",
		"December",
	];
	const weekDayLabels = enableWeeks
		? [
				"Vecka",
				"Mndag",
				"Tisdag",
				"Onsdag",
				"Torsdag",
				"Fredag",
				"Lrdag",
				"Sndag",
		  ]
		: [
				"Mndag",
				"Tisdag",
				"Onsdag",
				"Torsdag",
				"Fredag",
				"Lrdag",
				"Sndag",
		  ];

	let year: number = today.getFullYear();
	let row: Array<Day> = [];
	let rows: Array<Array<Day>> = [];

	const months: Array<Array<Array<Day>>> = [];
	const weeks: Array<Array<number>> = [];

	for (let month: number = 0; month <= 11; month++) {
		rows = [];

		const monthDate = new Date(year, month);
		const firstDay = monthDate.getDay();
		const oneJan = new Date(monthDate.getFullYear(), 0, 1);
		const diff = monthDate.getTime() - oneJan.getTime();
		const numberOfDays = Math.floor(diff / (24 * 60 * 60 * 1000));
		const monthWeeks: Array<number> = [];

		let date: number = 1;

		for (let i: number = 0; i < 5; i++) {
			row = [];
			const week = Math.floor((date + numberOfDays) / 7 + 1);
			monthWeeks.push(week);

			for (let j: number = 0; j < 7; j++) {
				if (i === 0 && j < firstDay - 1) {
					row.push({
						label: "",
						date: -1,
					});
				} else if (date > daysInMonth(month, year)) {
					row.push({ label: "", date: -1 });
				} else {
					row.push({
						label: Moon(year, month, date).emoji,
						date,
					});

					date++;
				}
			}

			rows.push(row);
		}

		weeks.push(monthWeeks);
		months.push(rows);
	}

	const maxSteps = 12;

	const handleNext = () => {
		setActiveStep(activeStep + 1);
	};

	const handleBack = () => {
		setActiveStep(activeStep - 1);
	};

	const currentMonth = months[activeStep];
	const currentMonthWeeks = weeks[activeStep];
	const dayElements: Array<JSX.Element> = [];
	const weekDayLabelElements: Array<JSX.Element> = [];

	weekDayLabels.forEach((aWeekDay) => {
		weekDayLabelElements.push(
			<div
				key={activeStep + "_" + aWeekDay}
				className={classes.cellLabel}
			>
				<span className={classes.label}>{aWeekDay}</span>
			</div>
		);
	});

	currentMonth.forEach((row, rowIndex) => {
		enableWeeks &&
			dayElements.push(
				<div
					key={activeStep + "_" + rowIndex + "_week"}
					className={classes.cell}
				>
					<div className={classes.week}>
						<span>{currentMonthWeeks[rowIndex]}</span>
					</div>
				</div>
			);
		row.forEach((day, dateIndex) => {
			if (day.date === -1) {
				dayElements.push(
					<div
						key={activeStep + "_" + rowIndex + "_" + dateIndex}
						className={classes.cell}
					>
						<div className={classes.cellContent}>
							<span className={classes.date}>&nbsp;</span>
						</div>
					</div>
				);
			} else if (
				day.date === today.getDate() &&
				activeStep === today.getMonth()
			) {
				dayElements.push(
					<div
						key={
							activeStep +
							"_" +
							rowIndex +
							"_" +
							dateIndex +
							"_" +
							day.date
						}
						className={classes.cellToday}
					>
						<div className={classes.cellContent}>
							<span className={classes.date}>{day.date}</span>
							{enableMoon && (
								<span className={classes.moon}>
									{day.label}
								</span>
							)}
						</div>
					</div>
				);
			} else {
				dayElements.push(
					<div
						key={
							activeStep +
							"_" +
							rowIndex +
							"_" +
							dateIndex +
							"_" +
							day.date
						}
						className={classes.cell}
					>
						<div className={classes.cellContent}>
							<span className={classes.date}>{day.date}</span>
							{enableMoon && (
								<span className={classes.moon}>
									{day.label}
								</span>
							)}
						</div>
					</div>
				);
			}
		});
	});

	return (
		<div className={classes.root}>
			<div className={classes.container}>
				<Paper square elevation={0} className={classes.header}>
					<div className={classes.flex1} />
					<Button
						size="small"
						className={classes.stepButton}
						onClick={() => {
							handleBack();
						}}
						disabled={activeStep === 0}
					>
						<KeyboardArrowLeft />
					</Button>
					<span className={classes.month}>
						{monthLabels[activeStep]}
					</span>
					<Button
						size="small"
						className={classes.stepButton}
						onClick={() => {
							handleNext();
						}}
						disabled={activeStep === maxSteps - 1}
					>
						<KeyboardArrowRight />
					</Button>
					<div className={classes.flex1} />
					<IconButton
						color="inherit"
						aria-label="open drawer"
						edge="end"
						onClick={handleDrawerOpen}
						sx={{
							...(open && { display: "none" }),
							marginRight: "20px",
							padding: "0px",
						}}
					>
						<MenuIcon />
					</IconButton>
				</Paper>
				<div
					className={
						enableWeeks
							? classes.gridWeekDay
							: classes.gridWeekDayNoWeeks
					}
				>
					{weekDayLabelElements}
				</div>
				<div
					className={enableWeeks ? classes.grid : classes.gridNoWeeks}
				>
					{dayElements}
				</div>
			</div>
			<Drawer
				sx={{
					width: open ? 240 : 0,
					flexShrink: 0,
					"& .MuiDrawer-paper": {
						width: 240,
						backgroundColor: "#000",
					},
				}}
				variant="persistent"
				anchor="right"
				open={open}
			>
				<Box sx={{ display: "flex", flexDirection: "column" }}>
					<IconButton
						onClick={handleDrawerClose}
						sx={{ color: "#FFF" }}
					>
						<ChevronRightIcon />
					</IconButton>
					<FormControlLabel
						label="Moon"
						sx={{ color: "#FFF" }}
						control={
							<Checkbox
								sx={{ color: "#FFF" }}
								checked={enableMoon}
								onChange={() => {
									setEnableMoon(!enableMoon);
								}}
							/>
						}
					/>
					<FormControlLabel
						label="Weeks"
						sx={{ color: "#FFF" }}
						control={
							<Checkbox
								sx={{ color: "#FFF" }}
								checked={enableWeeks}
								onChange={() => {
									setEnableWeeks(!enableWeeks);
								}}
							/>
						}
					/>
				</Box>
			</Drawer>
		</div>
	);
};

export default withStyles(style, { withTheme: true })(Calendar);
